import React, { useEffect, useState } from 'react'
import noteService from '../services/notes'

const Notes = () => {

    const [ notes, setNotes ] = useState([])
    const [ newNote, setNewNote ] = useState('')
    const [ newImportant, setNewImportant ] = useState(false)
    const [ isLoading, setIsLoading ] = useState(true)
    const [ error, setError ] = useState(null)

    useEffect(() => {
        noteService
            .getAll()
            .then(data => {
                console.log(data)
                setNotes(data)
                setIsLoading(false)
            })
            .catch(err => {
                console.log(err)
                setError(err)
                setIsLoading(false)
            })
    }, [])

    const addNote = event => {
        event.preventDefault()
        const noteObject = {
            content: newNote,
            important: newImportant
        }
      
        noteService
            .create(noteObject)
            .then(data => {
                console.log(data)
                setNotes(notes.concat(data))
                setNewNote('')
                setNewImportant(false)
          })
    }

    const notesList = notes.map(item =>
        <Note
            key={ item.id }
            note={ item }
            notes={ notes }
            updateNotes={ (newNotes) => setNotes(newNotes) }
        />)

    return (
        <div className="something">
            <h1>Add note</h1>
            <form onSubmit={ addNote }>
                <div>
                    Content <input type="text" onChange={ (e) => setNewNote(e.target.value) } value={ newNote }/>
                </div>
                <div>
                    Important <select onChange={ (e) => setNewImportant(e.target.value) }>
                        <option value="false">No</option>
                        <option value="true">Yes</option>
                    </select>
                </div>
                <input type="submit" value="Save"/>
            </form>
            <h1>Notes</h1>
            { isLoading ? <h3>Loading...</h3> : null }
            <table>
                <tbody>
                    { notes && !error ? notesList : <li>No notes have been found.</li> }
                </tbody>
            </table>
        </div>
    )
}

const Note = ({ note, notes, updateNotes, displayDialog }) => {

    const deleteNote = (e) => {
        const question = window.confirm(`Are you sure you want to delete this note?`)
        if (!question) return

        noteService
            .remove(note.id)
            .then(() => updateNotes(notes.filter(item => item.id !== note.id)))
            .catch(() => alert("Error trying to delete the note. Please refresh the page and try again."))
    }

    return (
        <tr>
            <td>{ note.date }</td>
            <td>{ note.content }</td>
            <td>
                <button onClick={ deleteNote }>Delete</button>
            </td>
        </tr>
    )
}

export default Notes